/* Login.css */
.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f7f7f7;
  }
  
  .login-form {
    width: 100%;
    max-width: 400px;
    margin: auto;
    padding: 2rem;
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
  }
  
  .login-form h1 {
    text-align: center;
    color: #333;
    margin-bottom: 2rem;
  }
  
  .login-form input {
    width: 100%;
    padding: 1rem;
    margin-bottom: 1rem;
    border: 1px solid #ddd;
    border-radius: 4px;
    box-sizing: border-box;
  }
  
  .login-form button {
    width: 100%;
    padding: 1rem;
    background-color: #5c67f2;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .login-form button:hover {
    background-color: #4a54e1;
  }
  
  .error-message {
    color: red;
    text-align: center;
  }
  
  /* 반응형 디자인 */
  @media (max-width: 768px) {
    .login-form {
      padding: 1rem;
    }
  }
  
  @media (max-width: 480px) {
    .login-form {
      padding: 0.5rem;
      margin: 0 1rem;
    }
  
    .login-form input,
    .login-form button {
      padding: 0.75rem;
    }
  }
  