/* Main.css */
:root {
    --main-color: #5c67f2; /* 메인 컬러를 유지 */
    --background-color: #f0f2f5; /* 부드러운 배경색으로 변경 */
    --text-color: #4a4a4a; /* 조금 더 부드러운 텍스트 색상으로 변경 */
    --card-bg-color: #ffffff; /* 카드 배경 색상을 유지 */
    --btn-hover-color: #6c70f3; /* 버튼 호버 색상을 약간 밝게 변경 */
    --border-radius: 8px; /* 테두리 둥글기 유지 */
}

body {
    background-color: var(--background-color);
    color: var(--text-color);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.MainWrap {
    max-width: 1200px;
    margin: auto;
    padding: 1rem;
}

.Title {
    text-align: center;
    color: var(--main-color);
    margin-bottom: 2rem;
    animation: slideDown 1s forwards;
}
  
  .Items_Wrap {
    display: grid;
    grid-template-columns: repeat(4, 1fr); /* 첫 번째 줄에 4개 항목 */
    grid-gap: 1rem;
  }
  
  .Item:nth-child(5) { /* "My Skills" 항목 */
    grid-column: 1 / -1; /* 첫 번째에서 마지막 컬럼까지 확장 */
  }
  .Item:nth-child(1) { /* "My Skills" 항목 */
    grid-column: 1 / -1; /* 첫 번째에서 마지막 컬럼까지 확장 */
  }
  .Item {
    background: var(--card-bg-color);
    padding: 1rem;
    border-radius: var(--border-radius);
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
    animation: slideUp 0.5s ease forwards;
  }
  
  .Item:hover {
    transform: translateY(-5px);
  }
  
  .ItemTitle {
    font-size: 1.25rem;
    color: var(--main-color);
  }
  
  .ItemContents {
    display: block;
    margin-top: 0.5rem;
  }
  
  .defBtn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    background-color: var(--main-color);
    color: white;
    border-radius: 50%;
    transition: background-color 0.3s;
  }
  
  .defBtn:hover {
    background-color: var(--btn-hover-color);
  }
  
  /* 애니메이션 */
  @keyframes slideUp {
    from {
      transform: translateY(100%);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
  
  /* 반응형 레이아웃 */
  @media (max-width: 768px) {
    .Items_Wrap {
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    }

  }
  
  @media (max-width: 664px) {
    .Item:nth-child(1) { /* "My Skills" 항목 */
        grid-column: 1/1
      }
  }
  
  @media (max-width: 480px) {
    .Items_Wrap {
      grid-template-columns: 1fr;
    }
  
    .Item {
      padding: 0.5rem;
    }
  
    .ItemTitle {
      font-size: 1rem;
    }
  }