.tic-tac-toe {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: Arial, sans-serif;
    padding-top: 50px;
    width: 100vw;
    height: 100vh;
}
.Title {
    margin-bottom: 20px;
    font-size: 24px;
    user-drag: none;
    user-select: none;
    -moz-user-select: none; /* Firefox 지원 */
    -webkit-user-drag: none; /* Webkit 브라우저 (Chrome, Safari) 지원 */
    -webkit-user-select: none; /* Webkit 브라우저 (Chrome, Safari) 지원 */
    text-align: center;
  }
.status {
    font-size: 24px;
    margin: 20px;
}

.board-row {
    display: flex;
}

.square {
    width: 15vw; 
    height: 15vw; 
    font-size: 5vw;
    border: 1px solid #ccc;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
.square:hover {
    background-color: #eee;
}

.restart-button {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.restart-button:hover {
    background-color: #0056b3;
}