/* Weather.css */

.Weather-Wrap {
    font-family: 'Arial', sans-serif;
    background-color: #f2f4f6;
    padding: 1rem;
    padding-top: 50px;
    border-radius: 15px;
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
}

.Weather-Wrap .Title {
    text-align: center;
    color: #333;
    margin-bottom: 1.5rem;
    user-drag: none;
    user-select: none;
    -moz-user-select: none;
    /* Firefox 지원 */
    -webkit-user-drag: none;
    /* Webkit 브라우저 (Chrome, Safari) 지원 */
    -webkit-user-select: none;
    /* Webkit 브라우저 (Chrome, Safari) 지원 */
}

.Weather-Wrap select {
    width: 40%;
    padding: 0.5rem 1rem;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin: 0.5rem 0 0.5rem 0;
    appearance: none;
    background-color: #fff;
    cursor: pointer;
    text-align: center;
    border: none;
    font-size: 2rem;
    background: none;


    /* 수정: 목록으로 변환하기 위해 size 속성 추가 */
    size: 5;
    /* 원하는 항목 수로 조절 */
}

.forecast-row {
    display: flex;
    width: 80%;
    overflow-x: auto;
    justify-content: space-between;
    overflow-y: hidden;
    height: 20vh;
}

.forecast-col {
    display: flex;
    flex-direction: column;
    width: 80%;
    overflow-x: auto;
    height: 50vh;
}



.forecast-entry {
    background-color: #ffffff;
    border-radius: 10px;
    padding: 0.5rem;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.05);
    width: 200px;
    text-align: center;
}



.forecast-col .forecast-entry {
    display: flex;
    align-items: center;
    background-color: #ffffff;
    border-radius: 10px;
    margin-top: 1rem;
    padding: 1rem;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.05);
    width: 100%;
    text-align: left;
}

.forecast-col .forecast-entry div {
    width: 25%;
    text-align: left;
}

.forecast-entry img {
    width: 3rem;
    height: 3rem;
    display: block;
    margin: auto;
}

.forecast_text {
    text-align: center;
    margin: 0.5rem 0;
    color: #333;
    width: 10rem;
    white-space: nowrap;
    font-size: 1rem;
}

/* modal */
.modal {
    display: flex;
    /* 수정: 화면 중앙에 표시하기 위해 flex로 설정 */
    justify-content: center;
    /* 수정: 가로 중앙 정렬 */
    align-items: center;
    /* 수정: 세로 중앙 정렬 */
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.4);
    color: #333;
}

.modal-content {
    background-color: #fefefe;
    padding: 20px;
    border: 1px solid #888;
    max-height: 80%;
    /* 수정: 모달 높이를 최대 80%로 제한 */
    overflow-y: auto;
    /* 수정: 필요할 때 스크롤바 표시 */
    width: 80%;
    border-radius: 10px;
    text-align: center;
    /* 수정: 내용 가운데 정렬 */
}

.modal h2 {
    font-size: 20px;
    font-weight: 400;
    margin: 15px;
}

.modal-content .modal-item {
    margin: 15px;
    border-bottom: 1px solid #888;
}

.modal-content .span {
    width: 100%;
    text-align: right;
}

.close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
    text-align: right;
}

.close:hover,
.close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}

.icon-clear-sky-day {
    color: #ffd700;
    /* 금색 */
}

.icon-clear-sky-night {
    color: #1e90ff;
    /* 밝은 파랑색 */
}

.icon-few-clouds-day {
    color: #f0e68c;
    /* 카키색 */
}

.icon-few-clouds-night {
    color: #add8e6;
    /* 연한 파랑색 */
}

.icon-scattered-clouds {
    color: #c0c0c0;
    /* 은색 */
}

.icon-broken-clouds {
    color: #808080;
    /* 회색 */
}

.icon-shower-rain {
    color: #00bfff;
    /* 딥 스카이 블루 */
}

.icon-rain-day {
    color: #1e90ff;
    /* 도저 블루 */
}

.icon-rain-night {
    color: #4682b4;
    /* 스틸 블루 */
}

.icon-thunderstorm {
    color: #ff4500;
    /* 오렌지 레드 */
}

.icon-snow {
    color: #fffafa;
    /* 스노우 화이트 */
}

.icon-mist {
    color: #f5f5f5;
    /* 화이트 스모크 */
}